.header {
  height: 70px;
  background-color: #fff;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
}
.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  padding: 0 25px;
}
.header .nav-link {
  font-weight: 700;
  font-size: 0.6875rem;
  letter-spacing: 3.28px;
  text-transform: uppercase;
  color: #a5a5a5;
  transition: 0.2s;
}
.header .nav-link:hover {
  color: #000;
}
.header-nav {
  position: absolute;
  top: 95px;
  left: 0;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  width: 100%;
  padding-bottom: 20px;
  display: none;
  transition: 0.3s;
}
.header-nav .nav-link {
  display: block;
  padding: 14px 20px;
}
.header .menu-btn {
  display: none;
  position: absolute;
  opacity: 0;
}
.header .menu-btn:checked ~ .header-nav {
  display: flex;
}
.header .menu-btn:checked ~ .menu-toggle span:nth-child(1) {
  transform: rotate(45deg) translate(2px, 5px);
}
.header .menu-btn:checked ~ .menu-toggle span:nth-child(2) {
  opacity: 0;
}
.header .menu-btn:checked ~ .menu-toggle span:nth-child(3) {
  transform: rotate(-45deg) translate(2px, -5px);
}
.header .menu-toggle {
  cursor: pointer;
}
.header .menu-toggle span {
  width: 20px;
  height: 2px;
  display: block;
  margin-bottom: 3px;
  background-color: #08a6e4;
  transition: 0.3s all ease-in-out;
}
.header .menu-toggle span:last-child {
  margin-bottom: 0;
}
@media screen and (min-width: 1125px) {
  .header .menu-toggle {
    display: none;
  }
}

